nav {
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  color: #888;
  display: flex !important;
  justify-content: space-between;
  overflow: hidden;
  height: 32px;
  padding-left: 8px;
  padding-right: 4px; }

nav ul {
  display: flex;
  height: inherit;
  margin: 0;
  padding: 0; }

nav ul li {
  display: flex;
  list-style-type: none;
  -webkit-transition: all 0.2s;
  transition: all 0.2s; }

nav > ul > li:hover {
  z-index: 1;
  border-bottom: 2px solid #1AA5B8 !important; }

.logo:hover {
  border: none !important; }

nav > ul > li > a > .caret {
  display: inline-block;
  margin-left: 5px;
  margin-bottom: 5px;
  width: 8px;
  height: 8px;
  border-top: 2px solid #000000;
  border-left: 2px solid #000000;
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg); }

nav > ul > li > a > .caret:before {
  display: block;
  height: 200%;
  width: 200%;
  margin-left: -50%;
  margin-top: -50%;
  content: "";
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }

nav > ul > li > a {
  color: #aaa;
  display: block;
  padding: 0 24px;
  text-decoration: none !important; }

nav > ul > li > div {
  background-color: white;
  border-top: 0;
  border-radius: 0 0 4px 4px;
  box-shadow: 0 2px 2px -1px rgba(0, 0, 0, 0.055);
  display: none;
  margin: 0;
  opacity: 0;
  position: absolute;
  width: 165px;
  visibility: hidden; }

nav > ul > li:hover > div {
  margin-top: 32px;
  display: block;
  opacity: 1;
  visibility: visible; }

nav > ul > li > div ul > li {
  display: block; }

nav > ul > li > div ul > li > a {
  color: #fff;
  display: block;
  padding: 4px 8px;
  text-decoration: none;
  line-height: 16px !important; }

